.header {
    position: fixed;
    width: 100%;
    padding: 40px 0;
    transition: .8s linear;
    z-index: 100;
    &__inner {
        @include flex-b-c;
        position: relative;
        .menu__link {
            &:hover {
                color: #555;
            }
        }
    }
    &__logo {
        @include flex-b-c;
        justify-content: center;
        svg {
            width: 64px;
            height: 53px;
            fill: #ffffff;
        }
        span {
            display: inline-block;
            margin-left: 10px;
            font-family: 'Raleway', san-serif;
            font-size: 28px;
            letter-spacing: -1px;
            color:#ffffff;
            text-transform: capitalize;
            font-weight: 400;
        }
    }
    &__links {
        display: flex;
        align-items: center;
        svg {
            margin-left: 20px;
            display: inline-block;
            width: 24px;
            height: 24px;
            fill: #ffffff;
            stroke: #ffffff;
            stroke-width: 0;
            @media (max-width: 620px) {
                width: 20px;
                height: 20px;
            }
        }
      @media (max-width: 620px) {
        .search {
           margin-left: 0;
        }
        .basket {
         display: none;
        }
      }
    }
}
.menu {
    &__list {
        @include flex-b;
        padding-right: 15px;
        margin-left: auto;
        li {
            padding: 15px;
        }
        @media (max-width: 1220px) {
            display: block;
            position: fixed;
            top: 0;
            left: 0;
            right: 100%;
            width: 100%;
            height: 100%;
            padding-top: 100px;
            padding-left: 20px;
            background-color: #352f2d;
            z-index: -1;
            transition: all .4s linear;
        }
    }
    &__link {
        display: block;
        font-size: 14px;
        color: #ffffff;
        font-weight: 300;
        text-transform: capitalize;
        transition: all .3s linear;
        &:hover {
            color: $mainClr;
        }
    }
}
.header-sticky {
    background-color: $mainClr;
    box-shadow: 0 5px 19px 0 rgba(0,0,0,.07);
    padding: 15px 0;
}
@media (max-width: 1220px) {
    .header__links {
        margin-left: auto;
    }
    .menu__burger {
        position: relative;
        margin-left: 30px;
        display: inline-block;
        width: 26px;
        height: 20px;
        cursor: pointer;
        span {
            position: absolute;
            content: '';
            background: #ffffff;
            width: 100%;
            height: 2px;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            &:nth-child(3) {
                top: 93%;
            }
        }
        @media (max-width: 620px) {
            width: 23px;
            height: 17px;
            margin-left: 20px;
        } 
    }
}
.menu__on {
    right: 0;
}
.menu__burger-active {
    transition: all .4s linear;
    span {
        transition: all .4s linear;
        &:nth-child(1) {
            transform: rotate(-45deg);
            top: 50%;
        }
        &:nth-child(3) {
            transform: rotate(45deg);
            top: 50%;
        }
        &:nth-child(2) {
            transition: all .2s linear;
            transform: translate(-50%, -50%);
            opacity: 0;
        }
    }
}

.search {
    &__box {
        display: none;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background-color: #352f2d;
        z-index: 9999;
    }
    &__close-icon {
        position: relative;
        display: inline-flex;
        margin: 70px 70px 0 auto;
        width: 30px;
        height: 30px;
        &:after,
        &:before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: #fff;
            border-radius: 2px;
        }
        &:after {
            transform: rotate(45deg);
        }
        &:before {
            transform: rotate(-45deg);
        }

    }
    &__form {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 70%;
        z-index: 10;
        background-color: #fff;
        box-shadow: 0 0rem 1rem rgba(0, 0, 0, 0.808)!important;
    }
    &__block {
        @include flex-b-c;
        height: 45px;
        @media (max-width: 425px) {
        height: 40px;
        }
    }
    &__input {
        border: 0;
        background-color: #fff;
        height: 100%;
        width: 100%;
        padding: 0 15px;
        font-size: 14px;
        line-height: 120%;
    }
    &__form-icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        fill: #555!important;
        margin-right: 15px;
        transition: .3s;
        border: none;
        cursor: pointer;
    }
}
